import {Component} from "@angular/core";
import {FormInterface, SiteInterface} from "../../../contracts";
import {SiteService} from "../../../services/site.service";
import {Router} from "@angular/router";

@Component({
    selector: 'app-site-add',
    template: require('./site-add.component.html'),
    styles: [require('./site-add.component.scss')]
})
export class SiteAddComponent {
    public form : FormInterface = <FormInterface>{};
    public site: SiteInterface = <SiteInterface>{};

    public constructor(public siteService: SiteService, public router: Router) {}


    public save() {
        this.form.submitting = true;

        this.siteService.create(this.site)
            .subscribe(
                res => {
                    this.router.navigate(['', 'system', 'settings', res.id, 'versions']);
                },
                err => {
                    this.form.errors = err.json();
                    this.form.submitting = false;
                }
            );

    }

}



