import {Component, OnInit} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {SiteService} from "../../../services/site.service";
import {SiteInterface, FormInterface} from "../../../contracts";

@Component({
    selector: 'app-site-edit',
    template: require('./site-edit.component.html'),
    styles: [require('./site-edit.component.scss')]
})
export class SiteEditComponent implements OnInit {
    public site: SiteInterface = <SiteInterface>{};
    public form: FormInterface = <FormInterface>{};

    public constructor(
        public router: Router,
        public _router: ActivatedRoute,
        public siteService: SiteService
    ) { }

    public ngOnInit() {

        this._router.params.subscribe(params => {
            this.siteService.get(params['id'])
                .subscribe(
                    response => {
                        this.site = response;
                    }
                );
        });
    }

    public save() {
        this.form.submitting = true;
        this.siteService.update(this.site.id, this.site)
            .subscribe(
                res => {
                    this.router.navigate(['', 'system', 'settings']);
                }
            );
    }

}



