import {NgModule} from "@angular/core";
import {RouterModule} from "@angular/router";
import {CommonModule} from "@angular/common";
import {FormsModule} from "@angular/forms";
import {HttpModule} from "@angular/http"
import {SiteListComponent} from "./site-list/site-list.component";
import { SiteEditComponent } from './site-edit/site-edit.component';
import { SiteAddComponent } from './site-add/site-add.component';
import { SiteVersionsAddComponent } from './site-versions-add/site-versions-add.component';
import { SiteVersionsListComponent } from './site-versions-list/site-versions-list.component';

const SYSTEM_SETTINGS_ROUTES = [
    {path: '', component: SiteListComponent, pathMatch: 'full'},
    {path: 'add', component: SiteAddComponent, pathMatch: 'full'},
    {path: ':id/edit', component: SiteEditComponent, pathMatch: 'full'},
    {path: ':id/versions', component: SiteVersionsListComponent, pathMatch: 'full'},
    {path: ':id/versions/add', component: SiteVersionsAddComponent, pathMatch: 'full'},
];

@NgModule({
    declarations: [
        // FileUploaderComponent,
        SiteListComponent,
        SiteEditComponent,
        SiteAddComponent,
        SiteVersionsAddComponent,
        SiteVersionsListComponent
    ],
    imports: [
        CommonModule,
        FormsModule,
        HttpModule,
        RouterModule.forChild(SYSTEM_SETTINGS_ROUTES)
    ]
})
export class SystemSettingsModule {

}



