import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {SiteVersionInterface} from "../../../contracts/site";
import {SiteService} from "../../../services/site.service";

@Component({
  selector: 'app-site-versions-list',
  template: require('./site-versions-list.component.html'),
  styles: [require('./site-versions-list.component.scss')]
})
export class SiteVersionsListComponent implements OnInit {
    public site_id;
    public uis:SiteVersionInterface[] = [];
    public constructor(
        public _router: ActivatedRoute,
        public siteService: SiteService
    ) { }

    public ngOnInit() {

        this._router.params.subscribe(params => {
            this.site_id = params['id'];
            this.siteService.getVersion(params['id'])
                .subscribe(
                    response => {
                        this.uis = response;
                    }
                );
        });
    }

}



