import {Component, OnInit} from "@angular/core";
import {SiteService} from "../../../services/site.service";
import {SiteInterface} from "../../../contracts";

@Component({
    selector: 'app-sites-table',
    template: require('./site-list.component.html'),
    styles: [require('./site-list.component.scss')]
})
export class SiteListComponent implements OnInit {
    public sites: SiteInterface[] = [];

    public constructor(public siteService: SiteService) {
    }

    public ngOnInit() {
        this.siteService.all()
            .subscribe(
                response => {
                    this.sites = response;
                } ,
                error => {
                    console.log(error);
                }
            );
    }

}



